.paragraph {
  border: #e0eaef solid 1px;
  padding: 0.4rem 0.6rem 0.2rem;
  position: relative;
  margin-bottom: 1rem;

  *:first-child {
    margin-top: 0;
  }

  .field--label-above {
    .field__label {
      font-size: .8rem;
      padding: 0;
    }
  }

  .paragraph--metadata {
   display: flex;
   justify-content: flex-end;
   align-items: center;

   & > .contextual {
     display: block;
     position: static;
     margin-left: 0.5rem;

     button.trigger {
       display: none;
     }

     .contextual-links {
       display: block;
       border: none;
       background-color: inherit;
       position: static;
       li.paragraphsedit-paragraph {
         background-color: inherit;

         a {
           position: relative; /* 1 */
           display: inline-block;
           box-sizing: border-box;
           cursor: pointer;
           -webkit-transition: all 0.1s;
           transition: all 0.1s;
           text-align: center;
           text-decoration: none;
           color: #333;
           border: 1px solid #a6a6a6;
           background-color: #f2f1eb;
           background-image: -webkit-linear-gradient(top, #f6f6f3, #e7e7df);
           background-image: linear-gradient(to bottom, #f6f6f3, #e7e7df);
           text-shadow: 0 1px hsla(0, 0%, 100%, 0.6);
           font-size: 0.75rem; /* 5 */
           font-weight: 600;
           -webkit-appearance: none; /* 3 */
           -moz-appearance: none; /* 3 */
           -webkit-font-smoothing: antialiased; /* 6 */
           padding: 0.5rem;
           border-radius: .8rem;
         }
       }
     }
   }

   .proof-label {
     display: block;
     color: #7b7b7b;
     line-height: 1;
     font-weight: 700;
   }
   & + .contextual {
     margin-top: 2px;
   }
  }
}

.field--name-field-accordion-display.field__item {
  font-style: italic;
  font-size: .9rem;
  margin: 1rem 0 0;
  color: #555;
}

ol.process-steps {
  list-style: none;
  counter-reset: process-counter;

  li {
    counter-increment: process-counter;

    &::before {
      content: counter(process-counter);
      border: 1px solid #999;
      float: left;
      margin-right: 1rem;
      border-radius: 50%;
      padding: .1rem .5rem;
    }

    .process-item {
      margin-left: 3rem;
    }
  }
}

.paragraph--type--list-of-link-teasers .field--name-field-title {
  font-size: 20px;
  font-weight: 700;
  font-family: Bitter, Georgia, Cambria, Times New Roman, Times, serif;
  line-height: 1.3;
  margin-bottom: .5em;
}

.usa-accordion-button .field__item,
.paragraph--type--collapsible-panel-item .field--name-field-title {
  background-color: #ddd;
  padding: .8rem;
  font-weight: 700;
}

.additional-info-title {
  border-bottom: 2px dotted #0071bb;
  color: #323a45;
}

.paragraph.paragraph--type--spanish-translation-summary {
  padding-left: calc(2rem - 7px);
  border-left: 7px solid #9bdaf1;
}

.custom-alert,
.field--name-field-alert >.field__item,
.field--name-field-alert-block-reference >.field__item {
    background-color: #f1f1f1f1 !important;
    padding: 1rem !important;

  .field--name-field-alert-title > .field__item {
    font-weight: 700;
    font-family: Bitter, Georgia, Cambria, Times New Roman, Times, serif;
    line-height: 1.3;
    margin-bottom: .5em;
    font-size: 24px;
  }
}
